/**
 *  aoe
 */

export default class FireZone {
    constructor(opt) {
        const { ctx, parent, range, damage, id } = opt;
        this.type = 'fire';
        this.ctx = ctx;
        this.id = id;

        // range 
        this.minRange = 20;
        this.maxRange = range;
        this.range = this.minRange;

        this.parent = parent;
        this.damage = damage;

        this.maxLife = 300;
        this.life = this.maxLife;

        this.x = parent.x;
        this.y = parent.y;
    }

    draw(ctx) {
        if (this.life > 0) {
            this.range = this.calcRange();
            ctx.save();
            ctx.fillStyle = 'rgba(255, 0, 0, 0.15)';
            ctx.beginPath();
            ctx.arc(this.x, this.y, this.range, 0, Math.PI * 2);
            ctx.fill();
            ctx.restore();
            this.life--;
        }
    }

    /**
     * range 
     */
    calcRange() {
        const minR = this.minRange;
        const maxR = this.maxRange;
        const timePassed = this.maxLife - this.life;
        const timeStop = 100;   // range

        const range = minR + Math.min(timeStop, timePassed) * (maxR - minR) / timeStop;
        return range;
    }

}


